/*login.css*/

*{margin:0;
    padding:0;
    box-sizing:border-box;
    font-family: "Pretendard",sans-serif;}
    
    body{display:flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: rgb(88, 88, 88);}
    
    .wrapper {
    width:420px;
    background:transparent;
    backdrop-filter: blur(20px);
    border: 2px solid rgba(255,255,255,.2);
    box-shadow: 0 0 10px rgba (0,0,0,.2);
    color:#fff;
    border-radius: 10px;
    padding: 30px 40px;
    }
    
    .wrapper h1 {font-size: 36px;
    text-align: center;
    }
    
    .wrapper .input-box {
        position: relative;
        width: 100%;
        height: 50px;
        margin: 30px 0;
    }
    
    .input-box input {
        width: 100%;
        height: 100%;
        background-color: transparent;
        border: none;
        outline: none;
        border:2px solid rgb(255, 255, 255, .2);
        border-radius: 40px;
        font-size: 16px;
        color: #fff;
        padding: 20px 45px 20px 20px;
    }
    
    .input-box input::placeholder{
        color: #fff;
    }
    
    .input-box i {
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 20px;
    }
    
    .wrapper .errortext { 
        font-size: 14.5px;
        margin: -15px 0 15px;
        text-align: center;
    }
    
    .errortext label input {
        accent-color: #fff;
        margin-right: 3px;
    }
    
    .errortext a {
        color: #fff;
        text-decoration: none;
    }
    
    .errortext a:hover {
        text-decoration: underline;
    }
    
    .wrapper .btn {
        width: 100%;
        height: 45px;
        background-color: #fff;
        border: none;
        outline: none;
        border-radius: 40px;
        box-shadow: 0 0 10px rgb(0,0,0 .1);
        cursor: pointer;
        font-size: 16px;
        color: #333;
        font-weight: 600;
    }
    
    .wrapper .register-link {
        font-size: 14.5px;
        text-align: center;
        margin: 20px 0 15px;
    }
    
    .register-link p a {
        color: #fff;
        text-decoration: none;
        font-weight: 600;
    }
    
    .register-link p a:hover {
        text-decoration: underline;
    }
    
    
    
    